        
/* CONFIGURA√á√ÉO GERAL */

html, body {
    height: 100%;
    margin: 0px;
    padding: 0;
    background-color: #fefefe;
    font-family: Arial, sans-serif;
    overflow-x: hidden;
    
}

/* === GERAL === */
.content-area {
  display: flex;
  justify-content: space-between; /* Garante que a sidebar e os tratamentos fiquem com espa√ßo entre si */
  padding: 10px;
  gap: 20px; /* Espa√ßo entre a sidebar e os tratamentos */
  flex-wrap: wrap; /* Garante que o conte√∫do n√£o ultrapasse a tela em telas pequenas */
}


.tratamento-card:hover {
    transform: translateY(-5px); 
    box-shadow: 0 20px 25px rgba(0, 0, 0, 0.15); /* Aumenta a sombra para dar destaque */
}

.tratamento-card .tratamento-content {
    display: flex;
    align-items: center;
    gap: 20px;
}

.tratamento-card img {
    width: 150px;
    height: 150px;
    margin: 0 auto 20px auto;
    border-radius: 50%;
    object-fit: cover;
}

/* TITULO DO TRATAMENTO */
.tratamento-info h3 {
    font-size: 30px;
    font-weight: bold;
    color: #050606;
    flex-grow: 1;
    margin-top: 0px;
}

/* Estilo do bot√£o de detalhes */
.tratamento-info .btn {
    text-decoration: none;
    background-color: #e7e7e7;
    color: rgb(0, 0, 0);
    padding: 10px 50px;
    border-radius: 5px;
    text-align: center;
    display: inline-block;
    margin-top: 1px;  /* Espa√ßo entre o fabricante e o bot√£o */
}

.tratamento-info .btn:hover {
    background-color: #c7cccc;
}

/* BARRA DE EFIC√ÅCIA */
.eficacia-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
    min-width: 260px;
    align-items: flex-start;
    text-align: left;
}

.eficacia-bar-container {
    width: 100%;
    background-color: #e0e0e0;
    height: 20px;
    border-radius: 25px;
    position: relative;
    margin-bottom: 5px;
    margin-top: 1px;
}

.efficacy-filled {
    height: 100%;
    border-radius: 8px;
    background-color: #0094FF;
}

.efficacy-marker {
    width: 14px;
    height: 14px;
    background-color: #0094FF;
    position: absolute;
    top: 0;
    left: calc(100% - 7px);
    transform: translateX(-50%);
    border-radius: 50%;
}

/* DESCRI√á√ÉO DO TRATAMENTO */
.tratamento-info p {
    font-size: 20px;  /* Aumentei o tamanho da fonte da descri√ß√£o */
    color: #000000;
    line-height: 1.6;
    gap: 1px;
    margin-top: 10px;
}


/* P√°gina geral */
.tratamentos-page {
  min-height: 100vh;
  background: #ffffff;
}

/* Layout de duas colunas */


/* Largura total suficiente para 320px (sidebar) + 24px (gap) + 1200px (cards) */
.tratamentos-layout {
  max-width: 1544px;     /* 320 + 24 + 1200 + margem de seguran√ßa */
  margin: 0 auto;
  padding: 24px 16px;
  display: grid;
  grid-template-columns:  280px 1fr; /* fixa a coluna de conte√∫do em 1200px */
  gap: 30px;
  align-items: start;
  box-sizing: border-box;
  transform: translateY(-8px);
  transition: background-color 0.8s ease, border-color 0.8s ease;
}

/* Cards ocupam a largura inteira da coluna de conte√∫do */
.tratamentos-list { width: 100%; }
.tratamento-card  { width: 100%; }




/* Sidebar (posi√ß√£o e largura) */
.sidebar {
  position: sticky;
  top: 100px;                 /* ajuste conforme a altura do Header */
  width: 260px;              /* <<< garante a largura menor */
  height: fit-content;
}



.left-sidebar {
  position: static;
  width: 100%;               /* herda os 260px da coluna */
  padding: 0;
  border: none;
  background: transparent;
  box-shadow: none;
  border: 5px;
}


/* √Årea de conte√∫do/lista */

.conteudo {
  min-width: 0;
}



.tratamentos-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 16px;
}


/* Card dos tratamentos (ajuste pr√≥ximo ao mock) */

.tratamento-card {
  width: 100%;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  background: #fff;
  padding: 24px;                 /* ‚Üë padding maior */
  box-shadow: 0 6px 16px rgba(0,0,0,0.08);
}


.tratamento-content {
  display: grid;
  grid-template-columns: 120px 1fr 300px;  /* imagem | infos | efic√°cia (mais larga) */
  gap: 24px;                                /* ‚Üë espa√ßo entre colunas */
  align-items: stretch;                     /* ‚Üê faz as colunas terem mesma altura */
  min-height: 300px;                        /* altura m√≠nima confort√°vel (ajuste se quiser) */
}



/* --- Coluna de imagem --- */
.tratamento-imagem .img-fluid {
  width: 210px;            /* ‚Üë imagem um pouco maior */
  height: 150px;
  object-fit: contain;
}


.tratamento-info h3:first-child {
  font-size: 22px;          /* nome do tratamento maior */
  font-weight: 700;
  margin: 0 0 6px 0;
  color: #111827;
}

.tratamento-info h3:nth-of-type(2) {
  color: #0094FF;
  font-size: 16px;          /* subt√≠tulo (tipo) */
  font-weight: 600;
  margin: 0 0 10px 0;
}

.tratamento-info p {
  font-size: 15px;          /* textos gerais maiores */
  line-height: 1.6;         /* ‚Üë leitura confort√°vel */
  color: #374151;
  margin: 6px 0;
}

.tratamento-info .btn {
  display: inline-block;
  margin: 10px 0;
  font-size: 14px;
}

/* --- Coluna de Efic√°cia: ocupa toda a altura --- */
.eficacia-container {
  display: flex;             /* ocupa a coluna e distribui verticalmente */
  flex-direction: column;
  justify-content: space-between; /* t√≠tulos/topo, barra/meio, prazo/baixo */
  gap: 12px;                 /* espa√ßamento interno entre elementos */
  height: 100%;              /* ‚Üê estica para a altura do card */
  padding: 20px 0;            /* leve respiro vertical */
}

/* T√≠tulos e subt√≠tulos */
.eficacia-title {
  font-size: 22px;           /* ‚Üë maior */
  font-weight: 700;
  color: #b1b5bdff;            /* cinza como no mock */
  margin: 0;
}

.eficacia-sub {
  color: #111827;
  font-weight: 700;
}

/* Barra de efic√°cia */
.eficacia-bar-container {
  position: relative;
  width: 100%;
  height: 20px;              /* ‚Üë barra mais alta */
  background: #E5E7EB;
  border-radius: 6px;
  margin: 6px 0 8px;
}

.efficacy-filled {
  height: 100%;
  background: #0094FF;
  border-radius: 6px;
}



/* Valores de efic√°cia */
.eficacia-range {
  font-size: 27px;           /* ‚Üë valores maiores */
  font-weight: 700;
  margin: 0 0 8px;
}

.eficacia-min {
  color: #777b84ff;
  font-weight: 500;
}

.eficacia-max {
  color: #111827;
  font-weight: 700;
}

/* Prazo */
.prazo-title {
  margin: 0;
  font-size: 16px;           /* ‚Üë */
  font-weight: 600;
  color: #000;
}

.prazo-value {
  margin: 4px 0 0 0;
  font-size: 18px;           /* ‚Üë */
  font-weight: 700;
  color: #737171ff;
}

/* Container rol√°vel para a lista de checkboxes */
.checkbox-list {
  max-height: 160px;              /* ajuste a altura conforme o mock */
  overflow-y: auto;
  border: 1px solid #cfd4da;      /* mesma fam√≠lia do select */
  border-radius: 8px;
  background: #fff;
  padding: 8px;
}

/* Cada linha de checkbox */
.checkbox-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 4px;               /* respiro entre itens */
  font-size: 14px;
  color: #1f2937;
}

/* Checkbox padr√£o e texto */
.checkbox-item input[type="checkbox"] {
  width: 16px;
  height: 16px;
}
.checkbox-text {
  line-height: 1.4;
}

/* (opcional) Dar um hover leve nos itens */
.checkbox-item:hover {
  background: #f9fafb;
  border-radius: 6px;
}

/* J√° existentes (chips) ficam com respiro */
.chips { display: flex; flex-wrap: wrap; gap: 8px; }
.chip {
  display: inline-flex; align-items: center; gap: 6px;
  padding: 6px 10px;
  border-radius: 14px;
  background: #eef2ff;
  color: #1f2937;
  font-size: 12px;
}
.chip-close {
  background: transparent;
  border: none;
  color: #6b7280;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
}

