/* ========= MOBILE ========= */
@media (max-width: 720px) {
  /* Layout da p√°gina: cards primeiro e filtros depois (voc√™ j√° queria isso) */
  .tratamentos-layout{
    display: flex !important;
    flex-direction: column !important;
    grid-template-columns: none !important;
    gap: 16px !important;
    max-width: 100% !important;
  }

  .conteudo{
    order: 1 !important;
  }

  .sidebar{
    order: 2 !important;
    width: 100% !important;
    position: static !important;
    top: auto !important;
  }

  .left-sidebar,
  .filtros{
    position: static !important;
  }

  /* --------- AQUI √â O PRINCIPAL: card empilhado no mobile --------- */
  .tratamento-card{
    padding: 16px !important;
  }

  .tratamento-content{
    flex-direction: column !important;
    align-items: center !important;
    gap: 14px !important;
  }

  .tratamento-imagem{
    width: 100% !important;
    display: flex !important;
    justify-content: center !important;
  }

  .tratamento-imagem img{
    max-width: 160px !important;
    width: 100% !important;
    height: auto !important;
    object-fit: contain !important;
  }

  .tratamento-info{
    width: 100% !important;
    text-align: center !important;
  }

  /* bot√µes centralizados e consistentes no mobile */
  .tratamento-info .btn{
    display: inline-flex !important;
    justify-content: center !important;
    align-items: center !important;
    margin: 8px auto 0 !important;
  }


  /* container da efic√°cia */
  .eficacia-container {
    width: 100% !important;
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    text-align: center !important;
  }

  /* t√≠tulo "Efic√°cia: Redu√ß√£o de sintomas" */
  .eficacia-title {
    text-align: center !important;
    width: 100% !important;
  }

  /* barra */
  .eficacia-bar-container {
    width: 100% !important;
    max-width: 320px !important;
    margin: 8px auto !important;
  }

  /* faixa percentual */
  .eficacia-range {
    text-align: center !important;
    width: 100% !important;
  }

  /* prazo */
  .prazo-title,
  .prazo-value {
    text-align: center !important;
    width: 100% !important;
  }
}

